@import '../../assets/scss/var.scss';
.Mu {
  &-menus {
    display: flex;
    height: 1.17rem;
    text-align: center;
    justify-content: space-between;
    align-items: center;
    font-size: 0.37rem;
    &-item {
      flex-grow: 1;
      span {
        padding-bottom: 0.13rem;
        border-bottom: 2px solid transparent;
      }
    }
    &-item--active {
      span {
        padding-bottom: 0.13rem;
        border-color: $color_text_highlight;
      }
    }
  }
}