.Mu-rank {
  &-pic {
    width: 100px;
    height: 100px;
  }
  &-list {
    overflow: hidden;
    flex-grow: 1;
    background-color: #333;
    font-size: 0.32rem;
    padding: 0 0.53rem;
    li {
      line-height: 33.3px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
  }
}