@import '../../assets/scss/var.scss';
.Mu-singers {
  &-shortcut {
    position: fixed;
    right: 0;
    width: 0.53rem;
    top: 50%;
    background-color: rgba(0,0,0,.3);
    transform: translate(0, -50%);
    border-radius: 0.27rem;
    text-align: center;
    ul {
      padding: 0.53rem 0;
    }
  }
  &-dot {
    padding: 3px;
    line-height: 1;
    font-size: 0.32rem;
    color: $color_text;
    &--active {
      color: $color_text_highlight;
    }
  }
}